<template>
  <div>
    <div class="wrapper">
      <div class="header">
        <div class="top">
          <i class="back" @click="back"></i>
          <span class="text">撤销交易</span>
        </div>
        <div class="img-wrapper" ref="imgWrapper">
          <img src="~common/image/mine/logo.png" />
        </div>
        <div class="store-name">
          美一天便利超市
        </div>
      </div>
      <div class="content">
        <ul class="list">
          <li class="item">
            <input type="text" placeholder="请输入交易流水号" v-model="tradeNo"/>
          </li>
          <li class="item">
            <input type="text" placeholder="请输入会员手机号/会员ID" v-model="memeberId"/>
          </li>
        </ul>
        <div class="operate-btn" @click="submit">
          确认撤销
        </div>
      </div>
    </div>
    <alert ref="alert" :message="alertMsg"></alert>
    <confirm ref="confirm" @confirm="confirm" :message="confirmMessage"></confirm>
  </div>
</template>

<script type="text/ecmascript-6">
  import Alert from 'base/alert/alert'
  import Confirm from 'base/confirm/confirm'

  export default {
    data () {
      return {
        tradeNo: '',
        memeberId: '',
        alertMsg: '',
        confirmMessage: ''
      }
    },
    methods: {
      showAlert() {
        this.$refs.alert.show()
      },
      showConfirm() {
        this.$refs.confirm.show()
      },
      back() {
        this.$router.back()
      },
      submit() {
        if(!this._check()){
          return
        }
        this.confirmMessage = `确认撤销流水号<span style="color:red;">${this.tradeNo}</span>么？`
        this.showConfirm()
      },
      confirm() {
        console.log('confirm')
      },
      _check(){
        let tradeNo = this.tradeNo
        let memeberId = this.memeberId
        if(!tradeNo){
          this.alertMsg = '请输入交易流水号'
          this.showAlert()
          return false
        }
        if(!memeberId){
          this.alertMsg = '请输入会员手机号/会员ID'
          this.showAlert()
          return false
        }
        return true
      }
    },
    components: {
      Alert,
      Confirm
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .wrapper
    position fixed
    left 0
    right 0
    top 0
    bottom 0
    background $color-background
    z-index 1
    .header
      display flex
      align-items center
      flex-direction column
      width 100%
      height 180px
      color #fff
      background #eb4747
      .top
        display flex
        align-items center
        height 44px
        .back
          position absolute
          background url("~common/image/back-white.png") no-repeat
          background-size contain
          width 8px
          height 15px
          left 15px
          top 15px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
        .text
          color #fff
          font-size $font-size-large
          font-weight bold
      .img-wrapper
        height 70px
        width 70px
        margin-top 11px
        img
          display block
          width 100%
          height 100%
      .store-name
        margin-top 14px
    .content
      .list
        padding-left 15px
        background #fff
        margin-top 20px
        .item
          height 52px
          border-bottom 1px solid #e6e6e6
          display flex
          align-items center
          color #333
          font-size $font-size-medium
          &:last-child
            border-bottom none
          input
            display block
            width 100%
            height 100%
            box-sizing border-box
            padding-left 15px
            padding-right 15px
            outline none
      .operate-btn
        margin-top 40px
        margin-left auto
        margin-right auto
        display flex
        height 40px
        width 280px
        align-items center
        justify-content center
        color #fff
        background #eb4747
        border-radius 5px
        font-size $font-size-large


</style>




