<template>
  <div>
      <div class="header">
        <span class="text">店家功能</span>
        <i class="icon" :class="{new: news.length}"></i>
      </div>
      <div class="wrapper">
        <ul class="list" ref="list">
          <li class="item qrcode" @click="goToQRcode">
            <i class="icon"></i>
            <span>收款二维码</span>
          </li>
          <li class="item undorecord" @click="goToUndoRecord">
            <i class="icon"></i>
            <span>撤销记录</span>
          </li>
          <li class="item traderecord" @click="goToTradeRecord">
            <i class="icon"></i>
            <span>交易记录</span>
          </li>
          <li class="item members" @click="goToMembers">
            <i class="icon"></i>
            <span>会员清单</span>
          </li>
          <li class="item payaccount" @click="goToPayAccount">
            <i class="icon"></i>
            <span>应还款账单</span>
          </li>
          <li class="item statement" @click="goToStatement">
            <i class="icon"></i>
            <span>结算单</span>
          </li>
        </ul>
      </div>
      <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import {setLoginPage} from 'common/js/mixin'

  export default {
    mixins: [setLoginPage],
    data () {
      return {
        news: []
      }
    },
    created () {
      window.onresize = () => {
        this._calcWidth()
      }
    },
    mounted () {
      this._calcWidth()
    },
    methods: {
      goToQRcode() {
        this.$router.push({
          path: '/features/qrcode'
        })
      },
      goToUndoRecord() {
        this.$router.push({
          path: '/features/undorecord'
        })
      },
      goToTradeRecord() {
        this.$router.push({
          path: '/features/traderecord'
        })
      },
      goToMembers() {
        this.$router.push({
          path: '/features/members'
        })
      },
      goToPayAccount() {
        this.$router.push({
          path: '/features/payaccount'
        })
      },
      goToStatement() {
        this.$router.push({
          path: '/features/statement'
        })
      },
      //根据屏幕宽度计算图标大小和间距
      _calcWidth() {
        let clientWidth = window.innerWidth
        let listNode = this.$refs.list
        let children = listNode.children
        let width,height,marginTop,marginBottom,count = 0
        for(let i = 0; i < children.length; i++){
          let child = children[i]
          count += 1
          if(clientWidth < 340) {
            width = 135
            height = 135
            marginTop = 15
            marginBottom = 15
          } else if(clientWidth < 400) {
            width = 150
            height = 150
            marginTop = 20
            marginBottom = 20
          } else {
            width = 165
            height = 165
            marginTop = 25
            marginBottom = 25
          }
          listNode.style['margin-top'] = `${marginTop}px`
          child.style['margin-bottom'] = `${marginBottom}px`
          child.style.width = `${width}px`
          child.style.height = `${height}px`
          if(count % 2) {
            child.style.left = `${(clientWidth-width*2)/3}px`
          } else {
            child.style.right = `${(clientWidth-width*2)/3}px`
          }
        }
      }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .header
    position fixed
    height 44px
    top 0
    left 0
    right 0
    background $color-header-background
    display flex
    justify-content center
    align-items center
    .text
      color $color-header-text
      font-size $font-size-large
      font-weight bold
    .icon
      background url("~common/image/header/message.png") no-repeat
      position absolute
      height 14px
      width 17px
      right 16px
      top 15px
      background-size contain
      &.new:after
        position absolute
        background #eb4747
        width 6px
        height 6px
        border-radius 3px
        top -4px
        right -2px
        content ''
  .wrapper
    position absolute
    left 0
    right 0
    top 44px
    bottom 50px
    .list
      margin-top 20px
      display flex
      justify-content space-between
      flex-wrap wrap
      .item
        width 150px
        height 150px
        border-radius 3px
        box-shadow  0 4px 15px 0 rgba(219, 219, 219, 0.4);
        margin-bottom 20px
        background #fff
        display flex
        flex-direction column
        align-items center
        position relative
        .icon
          margin-top 44px
        span
          position absolute
          bottom 22px
          font-size 13px
          color $color-text
        &.qrcode
          .icon
            background url("~common/image/features/qrcode.png") no-repeat
            width 45px
            height 45px
            background-size contain
        &.undorecord
          .icon
            background url("~common/image/features/undorecord.png") no-repeat
            width 45px
            height 36px
            background-size contain
        &.traderecord
          .icon
            background url("~common/image/features/traderecord.png") no-repeat
            width 34px
            height 44px
            background-size contain
        &.members
          .icon
            background url("~common/image/features/members.png") no-repeat
            width 40px
            height 39px
            background-size contain
            margin-top 48px
        &.payaccount
          .icon
            background url("~common/image/features/payaccount.png") no-repeat
            width 34px
            height 44px
            background-size contain
        &.statement
          .icon
            background url("~common/image/features/statement.png") no-repeat
            width 34px
            height 44px
            background-size contain

</style>


