<template>
  <transition name="slide">
    <div>
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">推荐码</span>
      </div>
      <div class="content-wrapper">
          <div class="content" ref="content">
            <div class="top">
              <div class="icon">
                <img src="~common/image/mine/logo.png" alt="">
              </div>
              <span class="text">美一天便利店</span>
            </div>
            <div class="qrcode-wrapper">
              <img src="~common/image/mine/qrcode.png" alt="">
            </div>
            <div class="bottom">
              我的推广二维码
            </div>
          </div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  export default {
    data () {
      return {

      }
    },
    created () {
      window.resize = () => {
        this._calcWidth()
      }
    },
    mounted () {
      this._calcWidth()
    },
    methods: {
      back() {
        this.$router.back()
      },
      _calcWidth() {
        let clientWidth = window.innerWidth
        let content = this.$refs.content
        let width
        if(clientWidth < 340) {
          width = 280
        } else {
          width = 320
        }
        content.style.width = `${width}px`
      }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-enter-active
    transition all 0.05s

  .header
    position fixed
    height 44px
    top 0
    left 0
    right 0
    background $color-header-background
    display flex
    justify-content center
    align-items center
    .back
      position absolute
      background url("~common/image/back.png") no-repeat
      background-size contain
      width 8px
      height 15px
      left 15px
      top 15px
      &:before
        position absolute
        left -10px
        right -10px
        top -10px
        bottom -10px
        content ''
    .text
      color $color-header-text
      font-size $font-size-large
      font-weight bold
  .content-wrapper
    position fixed
    left 0
    right 0
    top 44px
    bottom 0
    z-index 1
    background $color-background
    display flex
    justify-content center
    align-items center
    .content
      width 320px
      height 433px
      background #fff
      display flex
      flex-direction column
      .top
        margin 30px
        height 60px
        display flex
        align-items center
        .icon
          width 60px
          height 60px
          img
            display block
            width 100%
            height 100%
        .text
          margin-left 24px
          color $color-header-text
          font-size $font-size-large
      .qrcode-wrapper
        height 240px
        width 240px
        margin-left auto
        margin-right auto
        img
          display block
          width 100%
          height 100%
      .bottom
        margin-top 20px
        font-size $font-size-medium
        color #808080
        height 24px
        display flex
        align-items center
        justify-content center
</style>



